<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>modelValue</td>
<td>值</td>
<td>[String,Number]</td>
<td>''</td>
</tr>
<tr>
<td>clear</td>
<td>清除图标，还不能用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位提示</td>
<td>String</td>
<td>'请输入'</td>
</tr>
<tr>
<td>type</td>
<td>类型：text|password|textarea</td>
<td>String</td>
<td>'text'</td>
</tr>
<tr>
<td>showPasswordSwitch</td>
<td>显示密码开关，还不能用</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>size</td>
<td>尺寸：big|medium|small</td>
<td>String</td>
<td>'medium'</td>
</tr>
<tr>
<td>groupMode</td>
<td>分组模式，只有在有append或prepend的slot才有效</td>
<td>Boolean</td>
<td>false</td>
</tr>
